.neighborhood-overview {
}
.neighborhood-overview.section {
    position: relative;
}
/* Map & Info window */
.neighborhood-overview .map-wrap {
    background-color: $cream;
    padding: 15px;
    height: 200px;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}
@media (min-width: $(tablet)) {
    .neighborhood-overview .map-wrap {
        height: 300px;
    }
}
@media (min-width: $(desktop)) {
    .neighborhood-overview .map-wrap {
        display: block;
        height: 100vh;
        padding: 0;
        position: fixed;
        top: 0;
        max-width: initial;
        width: 100%;
    }
}
.neighborhood-overview .overview-map {
    height: 100%;
}
/* Hide default GM close button */
.neighborhood-overview .infoBox > img {
    display: none;
}
.neighborhood-overview .info-window {
    background-color: #fff;
    box-shadow: 1px 3px 10px 0 rgba(0, 0, 0, 0.5);
    margin: 0 auto;
    position: absolute;
    left: -50%;
    bottom: 65px;
    text-align: center;
    width: 100%;
    z-index: 2;
}
.neighborhood-overview .info-window .contents {
    padding: 30px 30px 0;
}
.neighborhood-overview .info-window .logo {
    margin: 0 0 10px;
    height: auto;
    max-width: 100%;
}
.neighborhood-overview .info-window h5 {
    color: #000;
    font-size: 22px;
    font-weight: 600;
    line-height: 1.3;
    margin: 0 0 3px;
    word-break: break-all;
}
.neighborhood-overview .info-window p {
    font-family: 'Open sans';
    font-size: 18px;
    font-weight: 600;
    line-height: 1.3;
}
.neighborhood-overview .info-window .map-directions {
    display: block;
    color: $blue;
    font-family: 'Open sans';
    font-size: 16px;
    font-weight: normal;
    line-height: 1.1;
    margin: 20px 0 25px;
    text-decoration: none;
}
.neighborhood-overview .info-window .map-directions span {
    text-decoration: underline;
}
.neighborhood-overview .info-window .map-directions::before {
    margin-right: 4px;
}
.neighborhood-overview .info-window .more {
    display: block;
    height: 46px;
    padding: 12px 0;
    position: relative;
    background-color: $purple_light;
    color: #fff;
    font-size: 18px;
    letter-spacing: 0.4px;
    text-align: center;
    text-decoration: none;
}
.neighborhood-overview .info-window .more::after {
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    top: 100%;
    border-left: 14px solid transparent;
    border-right: 14px solid transparent;
    border-top: 14px solid $purple_light;
    left: 50%;
    margin-left: -14px;
    z-index: 1;
}
